.nav_page,
.common {
    padding-left: 2rem;
    height: 3rem;
    display: flex;
    align-items: center;
    background-color: #121111;
}

ul {
    list-style: none;
}

ul>li {
    margin-right: 2em;
}

.common {
    margin-top: auto;
    margin-bottom: auto;
}

.rgt {
    position: absolute;
    right: 0;
}


.availability_toggle {
    height: 1.5em;
    width: 3em;
    padding: 2px;
    display: inline;
    border: whitesmoke 2px solid;
    border-radius: 3em;
}

.toggle_shape {
    width: 50%;
    height: 100%;
    border-radius: 2em;
}

.not_available {
    border: red 1px solid;
    background-color: rgb(198, 6, 6);
}

.available {
    border: green 1px solid;
    background-color: rgb(4, 152, 4);
    float: right;
}